
@charset "utf-8";
* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
/* 禁用iPhone中Safari的字号自动调整 */
html {
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    /* 字体大小设置为 100px */
    font-size: 100px;
}
html ,body {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    background-color: #fff;
}
/* 去除iPhone中默认的input样式 */
input {
    -webkit-appearance: none;
    resize: none;
}
/* 取消链接高亮  */
body,
div,
ul,
li,
ol,
h1,
h2,
h3,
h4,
h5,
h6,
input,
textarea,
select,
p,
dl,
dt,
dd,
a,
img,
button,
form,
table,
th,
tr,
td,
tbody,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    font-family: Microsoft YaHei;
}
/* 设置HTML5元素为块 */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}
/* 图片自适应 */
img {
    display: block;
    max-width: 100%;
    height: auto;
    width: auto\9;
    /* ie8 */
    -ms-interpolation-mode: bicubic;
    /*为了照顾ie图片缩放失真*/
}
*:focus {
    outline: none;
}
/* 清除输入框内阴影 */
input,
select,
textarea {
    border: 0;
    -webkit-appearance: none;
    -moz-appearance: none;
         appearance: none;
}
/* 禁用长按页面时的弹出菜单(iOS下有效) ,img和a标签都要加 */
img,
a {
    -webkit-touch-callout: none;
}
